<template lang="pug">
.collapse(ref="collapse")
  slot
</template>

<script>
export default {
  name: 'collapse',

  props: {
    value: Boolean,
  },

  mounted() {
    this.toggle()
  },

  methods: {
    toggle() {
      const content = this.$refs.collapse
      content.style.maxHeight = this.value ? content.scrollHeight + 'px' : 0
    },
  },

  watch: {
    value() {
      this.toggle()
    },
  },
}
</script>

<style scoped lang="sass">
.collapse
  max-height: 0
  overflow: hidden
  transition: max-height 0.2s ease-out
</style>
